<template>
    <div class="ecom__element ecom-element element__image-hotspot"
        :data-trigger="data.settings?.trigger"
    >
        <div
            v-if="image"
            class="ecom-element__image-hotspot--wrapper ecom-image-align ecom-flex ecom-column"
            :class="{
                'ecom-replace-notice': $helpers.isPreviewImage(JSON.stringify(image))
            }"
        >
            <picture
                class="element__image-hotspot--img ecom-image-default"
                v-html="$helpers.renImageResponsive(image, screens)"
            />
            <figcaption
                v-if="isCaption"
                class="ecom-image__caption ecom-w__full"
                v-html="imageHotspot.caption"
            />
            <div
                v-for="(item, index) in imageHotspot.items"
                :key="index"
                class="element__image-hotspot--text ecom-pa ecom-flex ecom-fl_center ecom-al_center"
                :class="{
                    'element__image-hotspot--btn-nolabel':
                        !item.hotspotLabel || item.hotspotLabel.length === 0
                }"
                :style="{
                    left: item.horizontalPosition + '%',
                    top: item.verticalPosition + '%'
                }"
                :data-source="item.source"
                :data-handle="item.product && item.product.value"
                :data-limit="item?.limit_words??20"
                data-stopdrag
                @mouseup="setEditingElement(index)"
                @mousedown="mouseDown($event, index)"
            >
                <!-- <span
                    v-if="!item.hotspotLabel || item.hotspotLabel.length === 0"
                    class="element__image-hotspot--sonar"
                /> -->
                <div class="element__image-hotspot--btn ecom-flex ecom-fl_center ecom-al_center" :style="cursorBtn">
                    <div
                        v-if="item.hotspotIcon && item.hotspotIcon.value"
                        class="element__image-hotspot--btn-icon ecom-flex ecom-fl_center ecom-al_center"
                        v-html="item.hotspotIcon && item.hotspotIcon.value"
                    />
                    <div v-else class="element__image-hotspot--btn-icon ecom-flex ecom-fl_center ecom-al_center">
                        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 512 512" width="16" height="16">
                            <g>
                                <g>
                                    <path d="M256,0C115.39,0,0,115.39,0,256s115.39,256,256,256s256-115.39,256-256S396.61,0,256,0z"
                                        fill="currentColor" />
                                </g>
                            </g>
                        </svg>
                    </div>
                    <div
                        v-if="item.hotspotLabel"
                        class="element__image-hotspot--btn-label"
                    >
                        {{ item.hotspotLabel }}
                    </div>
                </div>
                <div
                    class="element__image-hotspot--content"
                    :class="[
                        `element__image-hotspot--content-${item.tooltipSide}`,
                         imageHotspot.isHasArrow ? 'has-arrow' : '',
                        `arrow-${item.tooltipSide}`,
                        imageHotspot.tooltipAnimation
                    ]"
                    tabindex="0"
                >
                    <div class="ecom-hotspots-container-tooltip">
                        <div v-if="item.source === 'product' && !item.product">
                        Select a product to preview
                    </div>
                    <img
                        v-if="item.source === 'product'"
                        class="element__image-hotspot--content-image"
                       :class="{'ecom-hidden': !item?.fields?.includes('image')}"
                        v-bind="convertImageCard(item)"
                        loading="lazy"
                    >
                    <img
                        v-else
                        class="element__image-hotspot--content-image"
                        v-bind="convertImageCard(item)"
                        loading="lazy"
                    >
                    <component
                        :is="item.tag ? item.tag : 'h3'"
                        v-if="item.source === 'product'"
                        class="element__image-hotspot--content-title"
                        :class="{'ecom-hidden': item.source === 'product' && !item?.fields?.includes('title')}"
                    />
                    <component
                        :is="item.tag ? item.tag : 'h3'"
                        v-else class="element__image-hotspot--content-title"
                        v-text="item.source === 'custom' ? item.titleCard : ''"
                    />
                    <div v-if="item.source === 'product'" class="element__image-hotspot--content-prices" :class="{'ecom-hidden':  !item?.fields?.includes('price')}">

                    </div>
                    <div
                        class="element__image-hotspot--content-text ecom-html"
                        v-html="item.source === 'custom' ? item.textCard : ''"
                        :class="{'ecom-hidden': item.source === 'product' && !item?.fields?.includes('description')}"
                    />
                    <div class="ecom__image-hostpot--content-container ecom-button-default">
                        <a
                            v-if="item.source !== 'custom' && item.product"
                            :class="{'ecom-hidden': !item?.fields?.includes('link')}"
                            href="#"
                            class="element__image-hotspot--content-btn element__image-hotspot--content-btn-product"
                            target="__blank"
                            v-html="lang('View Details', 'view_more_link')"
                        />
                        <a
                            v-if="
                                item.source === 'custom' &&
                                item.buttonCard &&
                                item.buttonCard.length > 0"
                            :href="item?.linkButtonCard ? item.linkButtonCard.href : '#'"
                            class="element__image-hotspot--content-btn element__image-hotspot--content-btn-custom"
                            v-bind="item?.linkButtonCard"
                            >{{ item?.buttonCard }}</a>
                    </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import { Element, Javascript, Liquid } from '@/mixins';

export default {
    name: 'ImageHotSpots',
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/hotspot/',
    presets:true,
    mixins: [Element, Javascript, Liquid],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {
            drag: {
                currentEl: null,
                i: -1,
                started: false,
                x: 0,
                y: 0
            },
            jsreactives: [ 'items', 'trigger']
        };
    },
    computed: {
        hotSpot() {
            return this.data.settings;
        },
        cursorBtn() {
            let cursor = 'default';
            if (!this.exporting) {
                cursor = 'move';
            } else {
                cursor = this.imageHotspot.trigger === 'click' ? 'pointer' : 'default';
            }
            return { cursor };
        },
        css() {
            return `
                .ecom-hidden{
                    display:none !important;
                }
                .element__image-hotspot--btn{
                    transition: opacity .3s ease ;
                }
                .element__image-hotspot .element__image-hotspot--content {
                    outline: none;
                }
                .element__image-hotspot--text.element__image-hotspot--btn-nolabel {
                    margin-top: -10px;
                    margin-left: -10px;
                    min-width: 20px;
                    min-height: 20px;
                }
                .element__image-hotspot--btn-nolabel .element__image-hotspot--sonar {
                    border-radius: 50%;
                    backface-visibility: hidden;
                    perspective: 800px;
                    position: absolute;
                    top: -8px;
                    right: -8px;
                    bottom: -8px;
                    left: -8px;
                    display: block;
                    animation: ecom_aimation-pulse 2s ease infinite;
                    background-color: rgba(255,255,255,.5);
                }
                .element__image-hotspot--btn-nolabel .element__image-hotspot--btn {
                    position: relative;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    left: 0;
                    backface-visibility: hidden;
                    perspective: 800px;
                    z-index: 1;
                    transition: all .3s ease;
                }
                .element__image-hotspot--btn-label {
                    margin-left: 4px;
                    font-size: 14px;
                }
                .element__image-hotspot--content {
                    position: absolute;
                    z-index: 100;
                    width: 250px;
                }
                .element__image-hotspot:not([data-trigger="always"]) .element__image-hotspot--content:not(.actived) {
                    opacity: 0;
                    visibility: hidden;
                }
                .element__image-hotspot--content.fade.actived {
                    animation-name: fadeIn;
                }
                .element__image-hotspot--content-bottom.slide.actived {
                    animation-name: slideUp;
                }
                .element__image-hotspot--content-top.slide.actived {
                    animation-name: slideDown;
                }
                .element__image-hotspot--content-right.slide.actived {
                    animation-name: slideLeft;
                }
                .element__image-hotspot--content-left.slide.actived {
                    animation-name: slideRight;
                }
                .element__image-hotspot--content.has-arrow:after {
                    position: absolute;
                    content: '';
                    width: 8px;
                    height: 16px;
                    background: #ffffff
                }
                .element__image-hotspot--content.arrow-left.has-arrow:after {
                    clip-path: polygon(0 0, 100% 50%, 0 100%);
                    top: 50%;
                    right: 12px;
                    transform: translateY(-50%);
                }
                .element__image-hotspot--content.arrow-right.has-arrow:after {
                    clip-path: polygon(0 50%, 100% 0 , 100% 100%);
                    top: 50%;
                    left: 12px;
                    transform: translateY(-50%);
                }
                .element__image-hotspot--content.arrow-bottom.has-arrow:after,
                .element__image-hotspot--content.arrow-top.has-arrow:after {
                    width: 16px;
                    height: 8px
                }
                .element__image-hotspot--content.arrow-bottom.has-arrow:after {
                    clip-path: polygon(50% 0, 100% 100%, 0 100%);
                    transform: translateX(-50%);
                    top: 13px;
                    left: 50%;
                }
                .element__image-hotspot--content.arrow-top.has-arrow:after {
                    clip-path: polygon(0 0 , 100% 0 , 50% 100%);
                    transform: translateX(-50%);
                    bottom: 12px;
                    left: 50%;
                }
                .element__image-hotspot--content-left, .element__image-hotspot--content-right {
                    top: 50%;
                    transform: translateY(-50%)
                }
                .element__image-hotspot--content-top, .element__image-hotspot--content-bottom {
                    left: 50%;
                    transform: translateX(-50%);
                }
                .element__image-hotspot--content-left {
                    right: 100%;
                    padding-right: 20px;
                }
                .element__image-hotspot--content-right {
                    left: 100%;
                    padding-left: 20px;
                }
                .element__image-hotspot--content-top {
                    bottom: 100%;
                    padding-bottom: 20px;

                }
                .element__image-hotspot--content-bottom {
                    top: 100%;
                    padding-top: 20px
                }
                .element__image-hotspot--content .element__image-hotspot--content-image {
                    object-fit: cover;
                }
                .element__image-hotspot--content .element__image-hotspot--content-btn {
                    display: inline-flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    transition: color .25s ease,background-color .25s ease,border-color .25s ease,box-shadow .25s ease,opacity .25s ease;
                }
                .ecom-hotspots-container-tooltip.ecom-loading-image:before {
                    position: absolute;
                    content: '';
                    inset: 0;
                    background: rgba(0,0,0,0.6)
                }
                @keyframes ecom_aimation-pulse {
                    0%,100% {
                        transform: scale(1)
                    }

                    50% {
                        transform: scale(1.2)
                    }
                }
                @keyframes fadeIn {
                    from { opacity: 0; visibility: hidden; }
                    to   { opacity: 1; visibility: visible; }
                }
                @keyframes slideUp {
                    from { opacity: 0; visibility: hidden; transform: translate(-50%, 30px) }
                    to { opacity: 1; visibility: visible; transform: translateX(-50%) }
                }
                @keyframes slideDown {
                    from { opacity: 0; visibility: hidden; transform: translate(-50%, -30px) }
                    to { opacity: 1; visibility: visible; transform: translateX(-50%) }
                }
                @keyframes slideLeft {
                    from { opacity: 0; visibility: hidden; transform: translate(30px,-50%); }
                    to { opacity: 1; visibility: visible; transform: translateY(-50%) }
                }
                @keyframes slideRight {
                    from { opacity: 0; visibility: hidden; transform: translate(-30px,-50%); }
                    to { opacity: 1; visibility: visible; transform: translateY(-50%) }
                }
                .ecom__image-hostpot--content-container{
                    display:flex;
                }
                .element__image-hotspot--img img {
                    width: 100%;
                }
                .ecom-image-picture * {
                    transition:inherit;
                }
                @media only screen and (max-width: 480px) {
                    .element__image-hotspot--content{
                        top:50% !important;
                        left:50% !important;
                        transform: translate(-50%,-50%) !important;
                        position: fixed;
                        padding: 0 !important;
                    }
                    .ecom-slider .element__image-hotspot--content{
                        position: absolute;
                        top: 0;
                        transform: translate(-50%,-60%) !important;
                    }
                }
                @media only screen and (max-width: 767px) {
                    .element__image-hotspot--content.has-arrow:after {
                        display: none !important;
                    }
                }
            `;
        },

        image() {
            if(!this.data.settings){
                return
            }
            let { image = {}, size, size_custom } = this.data.settings;
            if (Object.keys(image).length === 0) {
                return null;
            }
            if (size === 'custom') {
                size = size_custom;
            }
            else if(size) {
                size = {width: size.split('x')[0], height: size.split('x')[1]};
            }

            return {
                alt: image.name,
                ...Object.keys(image)
                    .filter((k) => k.startsWith('value'))
                    .reduce((acc, cur) => {
                        acc[cur] = this.$helpers.resizeImage(image[cur], size);
                        return acc;
                    }, {}),
            };
        },

        isCaption() {
            return this.data.settings?.use_caption && this.data.settings?.caption;
        },

        imageHotspot() {
            return this.data.settings;
        },

        javascript() {
            if (this.imageHotspot && this.imageHotspot.items && this.imageHotspot.items.length) {
                this.image;
                return function () {
                    function stripContent(htmlStr) {
                        if (htmlStr && htmlStr.length > 0) {
                            const div = document.createElement('div');
                            div.innerHTML = htmlStr;
                            return div.textContent || div.innerText || '';
                        }
                        return '';
                    }

                    function truncateWords(value, length, overflowSuffix) {
                        if(!value || !length) return '';
                        let words = value.split(' ');
                        if(words.length < length)
                        {
                            return value;
                        }

                        return words.slice(0, length).join(' ') + (overflowSuffix || '')
                    }

                    if(!this.$el) return;
                    const $el = this.$el;
                    const elements = $el.querySelectorAll(
                        '.ecom__element.element__image-hotspot .element__image-hotspot--text'
                    );
                    if(!this.isLive)
                    {
                        $el.querySelectorAll('.element__image-hotspot--content-btn')
                            .forEach((btn) => {
                                btn.addEventListener('click', function(e){
                                    e.preventDefault();
                                    e.stopPropagation();
                                    let href = this.getAttribute('href');
                                    if (href && href.indexOf('/product') >= 0) {
                                        e.preventDefault();
                                        window.open(window.EComposer.routes.domain + href);
                                    }
                                });
                            });
                    }


                    // get again product info
                    const loadProduct = async  function(element){
                        element.querySelector('.element__image-hotspot--content-image').style.opacity = 0;
                        const loadingWrapper = element.querySelector('.ecom-hotspots-container-tooltip');
                        if(loadingWrapper) loadingWrapper.classList.add('ecom-loading-image');

                        if(!window.EComposer ||  !window.EComposer.getProduct )
                        {
                            console.log('EComposer theme helper not enabled');
                            return false;
                        }

                        const handle = element.getAttribute('data-handle');
                        const limit = element.getAttribute('data-limit');

                        var  result;
                        if(element.dataset.product)
                        {
                            result = JSON.parse(element.dataset.product);
                            if(result.handle !== handle)
                            {
                                result = await window.EComposer.getProduct(handle);
                            }
                        }
                        else
                        {
                            result = await window.EComposer.getProduct(handle);
                        }
                        if (result && result.id) {

                            const imageElement = element.querySelector(
                                '.element__image-hotspot--content-image'
                            );
                            const titleElement = element.querySelector(
                                '.element__image-hotspot--content-title'
                            );
                            const textElement = element.querySelector(
                                '.element__image-hotspot--content-text'
                            );
                            const buttonElement = element.querySelector(
                                '.element__image-hotspot--content-btn'
                            );
                            const priceElement = element.querySelector('.element__image-hotspot--content-prices');
                            if(imageElement)
                                imageElement.src = result.featured_image;

                            if(titleElement)
                                titleElement.innerText = result.title;

                            if(textElement)
                                textElement.innerText = truncateWords(stripContent(result.description),parseInt(limit??20),'...') || '';

                            if(buttonElement)
                                buttonElement.href = result.url;

                            if(priceElement)
                            {
                                let markup = `<span class="element__image-hotspot--content-price">${window.EComposer.formatMoney(result.price)}</span>`;
                                if(result.price < result.compare_at_price)
                                {
                                    markup+=  `<span class="element__image-hotspot--content-price--regular">${window.EComposer.formatMoney(result.compare_at_price)}</span>`;
                                }
                                priceElement.innerHTML = markup;
                            }
                            element.dataset.product = JSON.stringify(result);
                            if(loadingWrapper) loadingWrapper.classList.remove('ecom-loading-image');
                            element.querySelector('.element__image-hotspot--content-image').style.opacity = 1;
                        }


                    }
                    const windowWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
                    elements.forEach((element, index) => {
                        const btn = element.querySelector('.element__image-hotspot--btn');
                        const content = element.querySelector(
                            '.element__image-hotspot--content'
                        );
                        let itv;
                        if (btn && content) {
                            if(this.settings.trigger === 'hover'){

                            element.addEventListener('mouseover', () => {
                                addIndex(content);
                                content.classList.add('actived');
                                if(windowWidth < 768) {
                                    var userScroll = window.pageYOffset;
                                    window.document.addEventListener('scroll', function() {
                                        var newScroll = window.pageYOffset;
                                        if(userScroll - newScroll > 100 || newScroll - userScroll > 100){
                                            content.classList.remove('actived');
                                        }
                                    })
                                }
                                let source = element.getAttribute('data-source');
                                if(source === 'product')
                                {
                                    loadProduct(element)
                                    // clearTimeout(itv);
                                    // itv = setTimeout(()=>loadProduct(element),100);
                                }
                            });
                            element.addEventListener('mouseleave', function () {
                                deleteIndex(content);
                                content.classList.remove('actived');
                            });
                            }
                            else
                            {
                                btn.addEventListener('click', () => {
                                    if (!content.classList.contains('actived')) {
                                        addIndex(content);
                                        content.classList.add('actived', 'ecom_current_click');
                                        if(windowWidth < 768) {
                                            var userScroll = window.pageYOffset;
                                            window.document.addEventListener('scroll', function() {
                                                var newScroll = window.pageYOffset;
                                                if(userScroll - newScroll > 100 || newScroll - userScroll > 100){
                                                    content.classList.remove('actived');
                                                }
                                            })
                                        }
                                        let source = element.getAttribute('data-source');
                                        if(source === 'product')
                                        {
                                            clearTimeout(itv);
                                            itv = setTimeout(()=>loadProduct(element),500);
                                        }
                                        setTimeout(() => {
                                            content.focus();
                                            document.addEventListener('click', doClose);
                                            content.classList.remove('ecom_current_click');
                                        }, 300);

                                    } else {
                                        deleteIndex(content);
                                        content.classList.remove('actived');
                                    }
                                });
                            }
                        }
                    });

                    function closePopup() {
                        $el.querySelector('.element__image-hotspot--content.actived:not(.ecom_current_click)') && $el.querySelector('.element__image-hotspot--content.actived:not(.ecom_current_click)').classList.remove('actived');
                        document.removeEventListener('click', doClose);
                    }

                    function doClose(e) {
                        if (
                            e === undefined ||
                            !e.target.closest('.ecom-hotspots-container-tooltip')
                        ) {
                            closePopup();
                        }
                    }

                    function addIndex(content) {
                        let column = content.closest('.ecom-column.ecom-core'), row = content.closest('.ecom-row.ecom-core');

                        if(column) {
                            column.style.zIndex = 101;
                        }
                        if(row) {
                            row.style.zIndex = 101;
                        }
                    }
                    function deleteIndex(content) {
                        let column = content.closest('.ecom-column.ecom-core'), row = content.closest('.ecom-row.ecom-core');
                        if(column) {
                            column.style.zIndex = '';
                        }
                        if(row) {
                            row.style.zIndex = '';
                        }
                    }
                };
            }
            return function () {};
        },

        screens() {
            return this.$store.getters['builder/screens'];
        },

        settings() {
            return [
                {
                    group_title: 'Image',
                    params: [
                        {
                            type: 'picker',
                            label: 'Choose Image',
                            name: 'image',
                            options: {
                                type: 'image',
                                output: ['value', 'name'],
                                editAlt: true
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Image Size',
                            name: 'size',
                            value: '',
                            options: {
                                type: 'dropdown',
                                values: {
                                    '400x300': '400 x 300px',
                                    '800x450': '800 x 450px',
                                    '500x500': '500 x 500px',
                                    '': 'Original',
                                    custom: 'Custom'
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: 'size',
                            description:
                                'You can crop the original image size to any custom size. You can also set a single value for height or width in order to keep the original size ratio.',
                            name: 'size_custom',
                            options: {
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['size'] === 'custom';
                                    }
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'use_caption',
                            label: 'Use Caption?',
                            options: {
                                oneline: true,
                                values: {
                                    on: {
                                        label: 'YES',
                                        value: true
                                    },
                                    off: {
                                        label: 'NO',
                                        value: false
                                    }
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: 'text',
                            name: 'caption',
                            options: {
                                placeholder: 'Enter your caption',
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['use_caption'] === true;
                                    }
                                }
                            }
                        }
                    ]
                },
                {
                    group_title: 'Hotspots',
                    params: [
                        {
                            name: 'items',
                            type: 'group',
                            value: [],
                            options: {
                                add_text: 'Add item',
                                is_clear_all:false
                            },
                            title_unique: false,
                            params: [
                                {
                                    name: 'source',
                                    type: 'popup',
                                    label: 'Source',
                                    options: {
                                        type: 'dropdown',
                                        default: false,
                                        values: {
                                            product: 'Product',
                                            custom: 'Custom'
                                        }
                                    },
                                    css: {
                                        isCss: false
                                    }
                                },
                                {
                                    type: 'picker',
                                    label: 'Choose Product',
                                    name: 'product',
                                    options: {
                                        type: 'product',
                                        output: ['value', 'name', 'thumbnail'],
                                        editAlt: true,
                                        visible: {
                                            keep_data: false,
                                            condition: (data) => {
                                                return data['source'] === 'product';
                                            }
                                        }
                                    }
                                },
                                {
                                    type: 'checkbox',
                                    name: 'fields',
                                    label: 'Select fields to show',
                                    options:{
                                        visible:function(data){
                                            return data['source'] === 'product';
                                        },
                                        values:{
                                            title: 'Title',
                                            image: 'Image',
                                            link: 'Link',
                                            price: 'Price',
                                            description: 'Description'
                                        }
                                    }
                                },
                                {
                                    type: 'number',
                                    name: 'limit_words',
                                    label: 'How many words to show?',
                                    options: {
                                        min: 5,max:150,
