    methods: {
        setEditingElement(i) {
            this.$store.commit('builder/setEditingElement', this);
            if( !('tabOpened' in this.data.settings.items))
            {
                Object.defineProperty( this.data.settings.items, "tabOpened", { enumerable: false, writable: true } );

            }
            this.data.settings.items.tabOpened = i;
        },
        convertImageCard(item) {

            if(!item){
                return
            }
            let { thumbnailCard = {}, product, source = 'custom'} = item;
            if ((source === 'custom' && Object.keys(thumbnailCard).length === 0) ||
                (source === 'product') && !product) {
                return null;
            }
            return {
                alt: thumbnailCard.name,
                src: (source === 'custom' ? thumbnailCard?.value : product?.thumbnail) || 'images/placeholder.png'
            };
        },

        checkLabelHotspots(){
            return this.data.settings.items?.some((x) => x.isShowLabel && x.hotspotLabel);
        },

