                            {
                                position:0,
                                fields:[
                                    {
                                        alias:'justify-content',
                                        options:{
                                            label:'Alignment',
                                            css:{
                                                selector:'root .ecom-button-default'
                                            }
                                        }
                                    }
                                ]
                            },
                        ]
                    }
                },
                (this.isCaption) ? {
                    group_alias: 'text',
                    options:{
                        group_name: 'hotspotCation',
                        group_title: 'Caption',
                        selector: ' .ecom__element.element__image-hotspot .ecom-image__caption'
                    }
                } : null

            ].filter(x => x);
            return style;
        },
        mouseDown(e, i) {
            if (this.exporting) {
                return;
            }
            let $target = e.target.closest('.element__image-hotspot--text');
            // $targetRoot = $target.getBoundingClientRect();
            this.drag.currentEl = $target;
            this.drag.i = i;
            this.idoc().onmousemove = this.onMouseMove;
            this.idoc().onmouseup = this.onMouseUp;
        },

        onMouseUp() {
            this.idoc().onmousemove = null;
            this.idoc().body.style.userSelect = '';
            this.idoc().body.style.cursor = '';
            let { i, x, y } = this.drag;
            if (i > -1 && this.drag.started) {
                this.hotSpot.items[i].horizontalPosition = x;
                this.hotSpot.items[i].verticalPosition = y;
            }
            Object.assign(this.drag, {
                x: -1,
                y: -1,
                i: -1,
                started: false
            });
        },

        onMouseMove(e) {
            let $root = this.$el,
                rectRoot = $root.getBoundingClientRect();
            let x = Math.round(((e.x - rectRoot.left) / rectRoot.width) * 100),
                y = Math.round(((e.y - rectRoot.top) / rectRoot.height) * 100);
            if (Math.abs(x) > 5 || Math.abs(y) > 5) {
                this.idoc().body.style.userSelect = 'none';
                this.idoc().body.style.cursor = 'move';
                x = Math.max(0, Math.min(100, x));
                y = Math.max(0, Math.min(100, y));
                this.drag.started = true;
                this.drag.x = x;
                this.drag.y = y;
                this.drag.currentEl.style.left = x + '%';
                this.drag.currentEl.style.top = y + '%';
            }
        }
    }
};
</script>
